@use '../../utils/styles/animations.scss';

.result {
  display: grid;
  row-gap: 16px;
  max-width: max-content;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  opacity: 0;

  &[data-animation~='fadeIn'] {
    animation: fadeIn 1s 0.5s forwards;
  }

  &[data-animation~='slideToBottom'] {
    animation: slideToBottom 1s forwards;
  }
}

.title {
  font-weight: 700;
  font-size: 56px;
  line-height: 67px;
  text-align: center;

  color: var(--clr-white);

  text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
  pointer-events: none;
}

.button {
  padding-inline: 61px;
  padding-block: 14.5px;

  background: linear-gradient(0deg, #f3f3f3 0%, #ffffff 100%);
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
  border-radius: 8px;

  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.5px;

  color: var(--clr-dark-text);
}
